.grid {

  &--mob {
    display: grid;
    grid-template-columns: repeat( 2, 1fr);
    column-gap: 20px;
  }

  @include onTablet() {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 24px;
  }

  @include onDesktop() {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 24px;
  }

  &--tablet,
  &--desktop {

    @include onTablet() {
      display: grid;
    }

    @include onDesktop() {
      display: grid;
    }
  }

  &__item {

    @for $start from 1 through 6 {
      @for $end from $start through 6 {
        &--tablet-#{$start}-#{$end} {
          @include onTablet() {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }

    @for $start from 1 through 12 {
      @for $end from $start through 12 {
        &--desktop-#{$start}-#{$end} {
          @include onDesktop() {
            grid-column: #{$start} / #{$end + 1};
          }
        }
      }
    }
  }
}
