.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 56px;
  border-radius: 40px;
  font-weight: 700;
  font-size: 16px;
  line-height: 27px;
  text-decoration: none;
  color: $color-menu;
  background-color: #fff;
  transition: all 0.5s;
  cursor: pointer;

  &:hover {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.4);
  }

  &:active {
    box-shadow: 1px 4px 16px rgba(232, 232, 240, 0.64);
  }

  &--last {
    margin-bottom: 48px;

    @include onTablet {
      margin-bottom: 0;
    }
  }
}
